.imageforitem{
    border-radius: 0px;
}
.OrderContainer{
    width: 100%;
    align-items: center;
    background-color: rgb(211, 213, 217);
    border-radius: 10px;
    margin-left: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    
}
.salesMainContainer{
    overflow: hidden;
    background-color: rgb(225, 227, 229);
}
.unkonw{
    flex: 5;
    margin-left: -10px;
}

.MainContainer{
    display: flex;
}

.sidebarr{
    flex: 1;
}
.mainDiv1{
    -webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.24); 
    box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.24);
    padding: 10px;
}

.allorder{
    color: rgb(59, 59, 59);
   font-family: 'Source Sans Pro'
}
.Revanue{
    margin-left: 20px;
}

.address{
    margin-left: 15px;
}

@media screen and (max-width:1036px) {
    .sidebarr{
        display: none;
    }
}

@media screen and (max-width:838px) {
    .sidebarr{
        display: none;
    }
}

@media screen and (max-width:712px) {
    .subOrderItme{
        display: block;
    }
    .mainDiv1{
        display: block;
    }
    .address{
        margin-left: 10px;
    }
    .imageforitem{
        width: 100%;
        object-fit: cover;
        height: 40vh;
        margin-left: -0px;
    }
}